import share from '@/components/share';
import uniSegmentedControl from "@/components/uni-segmented-control/uni-segmented-control.vue"
import listCell from '@/components/mix-list-cell';
import vinCar from './components/vin-car';
import statusCar from './components/status-car';
export default{
	components: {
		share,
		uniSegmentedControl,
		listCell,
		statusCar,
		vinCar
	},
	data() {
		return {
			specClass: 'none',
			specSelected:[],
			carDetail: {
				"cars" : {},
				"shop" : {},
				"user" : {},
				"customer" : {},
				"carimages" : [],
			},
			allParamShow: false,
			items: ['车辆参数','发动机参数','底盘及制动','其他配置'],
			itemStatus: ['非常规技术检查','静态检查','动态检查'],
			currentCard: 0,
			currentStatus: 0,
			carVin: {},
			transmissionConfig: [],
			imgUrl: 'http://tclapi.simaxian.com',
			tabCurrentIndex: 0,
			recomList: [],
			favorite: true,			
		};
	},
	async onLoad(options){
		
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let id = options.id;
		if(id){
			this.$api.msg(`点击了${id}`);
		}
		
		
		//规格 默认选中第一条
		/* this.specList.forEach(item=>{
			for(let cItem of this.specChildList){
				if(cItem.pid === item.id){
					this.$set(cItem, 'selected', true);
					this.specSelected.push(cItem);
					break; //forEach不能使用break
				}
			}
		})
		this.shareList = await this.$api.json('shareList'); */
		this.carDetail = await this.$api.json('carDetail');
		this.carVin = await this.$api.json('carVin');
		this.carCheckStatus = await this.$api.json('checkStatus');
		this.recomList = await this.$api.json('recomList');
		this.transmissionConfig = await this.$api.config('transmissionConfig');
			
		if(Object.keys(this.carVin).length != 0){ //有vin码返回值时
			this.allParamShow = true
			this.$refs.sonVinInfo.sonAssginVin(this.carVin);
		}
		
		this.$refs.sonStatusInfo.sonAssginStatus(this.carCheckStatus);
		
		console.log('empty', Object.keys(this.carVin).length)
		console.log('carVin', this.carVin)
		console.log(this.carDetail)
		console.log(this.transmissionConfig)
	},
	methods:{
		//规格弹窗开关
		toggleSpec() {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
		},
		onClickItem(e) {
		  if (this.$refs.sonVinInfo.currentCard !== e.currentIndex) {
		      this.$refs.sonVinInfo.currentCard = e.currentIndex;
		  }
		},
		onClickStatusItem(e){
			if (this.$refs.sonStatusInfo.currentStatus !== e.currentIndex) {
			    this.$refs.sonStatusInfo.currentStatus = e.currentIndex;
			}
		},
		//选择规格
		selectSpec(index, pid){
			let list = this.specChildList;
			list.forEach(item=>{
				if(item.pid === pid){
					this.$set(item, 'selected', false);
				}
			})

			this.$set(list[index], 'selected', true);
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = []; 
			list.forEach(item=>{ 
				if(item.selected === true){ 
					this.specSelected.push(item); 
				} 
			})
			
		},
		//分享
		share(){
			this.$refs.share.toggleMask();	
		},
		//收藏
		toFavorite(){
			this.favorite = !this.favorite;
		},
		buy(){
			uni.navigateTo({
				url: `/pages/order/createOrder`
			})
		},
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			console.log(item)
			// return false
			let id = item.ID;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		stopPrevent(){}
	},

}
